import React, { useState, useEffect} from 'react';
import Checkbox from '../components/Checkbox';
import { pdfjs } from 'react-pdf';
import '../styles/EventRegister.css';
import AgreePdf from '../domestic_ms_event_2301.pdf';

function EventRegister() {
  const [isAgreeChecked, setIsAgreeChecked] = useState(false);
  const [isPdfModalOpen, setIsPdfModalOpen] = useState(false);
  const handleAgreeCheckboxChange = () => {
    setIsAgreeChecked(!isAgreeChecked);
    setIsPdfModalOpen(!isPdfModalOpen); // PDF  
  };

  const handleJoinClick = () => {
    if (!isAgreeChecked) {
      alert('      .');
      return;
    }
      alert('  .')
    //    ( )
    console.log(' ');
  };

  pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.mjs`;

  const handlePdfClick = () => {
    window.open(AgreePdf, '_blank');
    setIsAgreeChecked(true);
    setIsPdfModalOpen(true); // PDF  
  };

  // const [isPdfLoaded, setIsPdfLoaded] = useState(false);
  const [pdfLoadingError, setPdfLoadingError] = useState(false);

  useEffect(() => {
    const loadPdf = async () => {
      if (isPdfModalOpen) {
        try {
          // PDF  
          window.open(AgreePdf, '_blank');
          // setIsPdfLoaded(true);
        } catch (error) {
          console.error('PDF  :', error);
          setPdfLoadingError(true);
        }
      }
    };

    loadPdf();
  }, [isPdfModalOpen]);

  return (
    <div id="joinPage">
        <div>
            <Checkbox 
              checked={isAgreeChecked} 
              onChange={handleAgreeCheckboxChange} 
              iconOn="../ic_checkbox_24_enable_ON.svg" 
              iconOff="../ic_checkbox_24_enable_OFF.svg" 
              label ="    " 
              labelClass="labelInfo"
            />
        </div>
        <div id="divLine01"></div>
        <div>
            <Checkbox 
              checked={isAgreeChecked} 
              onChange={handleAgreeCheckboxChange} 
              iconOn="../ic_checkbox_20_bg_white_enable_ON.svg" 
              iconOff="../ic_checkbox_20_bg_white_enable_OFF.svg" 
              label="  ." 
              labelClass="labelAgree"
            />
      
                <button id="infoPdf" onClick={handlePdfClick}>{isPdfModalOpen ? '' : ''}</button>
                {isPdfModalOpen && (
                <div className="modal">
                  {pdfLoadingError && <div>PDF  .</div>}
                  {/* {isPdfLoaded ? (
                    window.open(AgreePdf, '_blank')
                  ) : (
                    <div></div>
                  )} */}
              </div>
                )} 
            
            <a href="/domestic_ms_event_2301.pdf" target='_blank'> </a>
        </div>
        <div id="divLine02"></div>
        <div>
            <button className="btn Search">
                 
            </button>
        </div>
        <div>
            <button className="btn Join" onClick={handleJoinClick}>
                 
            </button>
        </div>
    </div>
  );
}

export default EventRegister;